.title {
	@include circe;
	@include f(30px, 700, 1.2);
	text-transform: uppercase;
	color: $brown;
	@include md {
		font-size: 26px; }
	&_lg {
		font-size: 30px;
		letter-spacing: -0.015em;
		@include md {
			font-size: 30px; } }
	&_m {
		font-size: 23px;
		line-height: 1.5;
		letter-spacing: -0.02em;
		@include lg {
			font-size: 21px; }
		@include sm {
			font-size: 16px; } }
	&_s {
		@include f(22px, 700, 1);
		color: $white;
		@include lg {
			font-size: 18px; } } }

.title-s {
	@include circe;
	@include f(22px, 700);
	text-transform: uppercase; }


.description {
	@include roboto;
	@include f(20px, 400);
	color: $black;
	@include lg {
		font-size: 14px; } }
