.form-main {
	border: 10px solid $coffee-opa;
	padding: 38px 50px 35px;
	background-color: $white-opa;
	max-width: 840px;
	width: 100%;
	position: relative;
	@include lg {
		padding: 40px;
		max-width: 780px; }
	@include sm {
		border: 5px solid $coffee-opa;
		padding: 20px; }
	&__title {
		margin-bottom: 30px;
		text-align: left; }
	&__descr {
		margin-top: 10px; }
	&__btn {
		position: absolute;
		top: 25px;
		right: 25px; } }
