.dishful {
	margin-bottom: 42px;
	&__inner {
		position: relative; }
	.icon {
		color: $brown; }
	.menu-slider__text {
		max-width: none;
		padding: 0 100px;
		text-align: center; }
	.menu__next-arrow {
		right: 71px; } }

.choice {
	&__header {
		background-color: $powder; }
	&__current {
		display: none; }
	&__list {
		display: flex;
		justify-content: center;
		align-items: center; }
	&__title {
		width: 33.3%;
		text-align: center;
		padding: 10px 20px;
		cursor: pointer;
		transition: background-color .3s; }
	&__body {
		padding-bottom: 24px;
		[data-tabs="home"] & {
			background-color: $powderDark; }
		[data-tabs="chef"] & {
			background-color: $pinkLight; }
		[data-tabs="light"] & {
			background-color: $greenLight; } }
	&__info {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px;
		padding-bottom: 19px; }
	&__select {
		display: flex;
		padding-bottom: 17px;
		padding-top: 10px; }
	&__text {
		font-size: 20px;
		letter-spacing: .06em; }
	&__basket {
		color: $brown; }
	&__dishes {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px; }
	&__item {
		width: 33.3%;
		padding: 0 10px 30px 10px;
		position: relative; } }


.main-dish {
	background-color: $white;
	position: relative;
	&__inner {}
	&__link {
		display: block;
		overflow: hidden;
		position: relative;
		@include hover {
			&:before {
				opacity: 1; } }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: rgba($black, .2);
			z-index: 1;
			opacity: 0;
			transition: opacity .3s; } }
	&__img {
		width: 100%;
		height: 0;
		padding-bottom: 74.5%;
		@include cover; }
	&__title {
		@include roboto;
		@include f(22px, 400);
		letter-spacing: normal;
		text-transform: uppercase;
		color: $cardTitle;
		padding: 31px 23px 10px;
		@include lg {
			padding: 31px 18px 10px; } }
	&__specification {
		display: flex;
		justify-content: space-between;
		padding: 25px 16px 15px;
		@include lg {
			padding: 25px 11px 15px; } }
	&__check {
		margin-top: 71%;
		top: 0;
		right: 16px;
		z-index: 2;
		position: absolute;
		@include lg {
			margin-top: 65%; } } }



.check {
	width: 55px;
	height: 55px;
	display: inline-block;
	vertical-align: top;
	position: relative;
	cursor: pointer;
	font-size: 22px;
	user-select: none;
	.person & {
		width: 100%;
		height: 100%; }
	&__text {
		position: relative;
		padding-left: 40px;
		font-size: 15px;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			@include circle(26);
			background-color: $white; }
		&:after {
			content: '';
			position: absolute;
			top: calc(50%);
			transform: translateY(-50%);
			left: 3px;
			@include circle(20);
			background-color: transparent;
			transition: background-color .2s; } }
	input {
		position: absolute;
		opacity: 0;
		height: 0;
		width: 0;
		&:checked {
			+ .check__checkmark {}
			+ .check__text:after {
				background-color: $green; } }
		&:checked {
			+ .check__checkmark:after {
				background-color: $green; } } }
	&__checkmark {
		position: absolute;
		top: 0;
		left: 0;
		@include circle(55);
		background-color: $white;
		border: 1px solid $brown;
		&:after {
			content: "";
			position: absolute;
			@include vcenter;
			@include circle(40);
			background-color: $grayMid;
			transition: background-color .2s; } }
	&__text {
		display: inline-block; } }


.select {
	user-select: none;
	position: relative;
	&:not(:first-child) {
		margin-left: 31px; }
	.btn {
		@include roboto;
		@include f(14px, 500);
		color: $brown;
		text-transform: uppercase;
		padding: 8px 45px 8px 12px;
		max-width: 138px;
		background-color: transparent;
		border: 1px solid $brown;
		border-radius: 20.5px;
		&:focus {
			outline: none!important; } }
	.dropdown {
		max-width: 138px;
		&-toggle {
			&:after {
				content: '';
				position: absolute;
				margin: 3px 0 0 0;
				vertical-align: top;
				border: none;
				background-image: url(../img/arrow-down.svg);
				@include cover;
				width: 16px;
				height: 8px;
				top: 9px;
				right: 15px; } } }
	.bootstrap-select .dropdown-toggle .filter-option {
		position: relative;
		padding: 0; }
	.filter-option {
		&-inner-inner {
			overflow: auto; } }
	.dropdown-menu {
		margin-top: -40px;
		background-color: rgba(235,235,235,.8);
		padding-top: 40px;
		padding-bottom: 10px;
		border-radius: 20.5px 20.5px 0 0;
		border: none;
		&.inner {
			background-color: transparent; }
		&[x-placement="top-start"] {
			margin-top: auto;
			margin-bottom: -40px;
			padding-top: 10px;
			padding-bottom: 40px;
			border-radius: 0 0 20.5px 20.5px; } }
	.inner {
		padding: 0; }
	.dropdown-item {
		@include roboto;
		@include f(18px, 500);
		color: $brown;
		padding: 12px 20px;
		text-transform: uppercase;
		transition: color .3s;
		background-color: transparent;
		@include hover {
			color: $green;
			background-color: transparent; } } }




.bg_pow {
	// transition: background-color .3s
	&.is-active {
		background-color: $powderDark; } }
.bg_pink {
	// transition: background-color .3s
	&.is-active {
		background-color: $pinkLight; } }
.bg_green {
	// transition: background-color .3s
	&.is-active {
		background-color: $greenLight; } }
