.stock-page {
	padding-top: 46px;
	&__pagination {
		display: flex;
		justify-content: flex-end;
		padding-bottom: 24px;
		padding-right: 6px; }
	&__item {
		margin-bottom: 40px; } }

.offer {
	display: flex;
	&__img {
		flex-shrink: 0;
		width: 39%;
		min-height: 355px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center; }
		.blog & {
			min-height: 484px;
			width: 50%; } }
	&__content {
		position: relative;
		padding: 40px 0 30px 50px;
		background-color: $orange-bg;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 100%;
			width: 200%;
			height: 100%;
			background-color: $orange-bg; } }
	&__link {
		display: block;
		color: $green;
		text-decoration: underline;
		font-size: 20px;
		font-weight: 700;
		@include circe;
		padding-bottom: 30px; }
	&__date {
		color: $pink;
		padding-bottom: 8px;
		text-transform: uppercase;
		.blog & {
			color: $brown;
			font-size: 20px;
			padding-bottom: 25px; } }
	&__title {
		color: $brown;
		line-height: 1.2;
		padding-bottom: 15px;
		text-transform: uppercase;
		.blog & {
			font-size: 20px;
			padding-bottom: 11px; } }
	p {
		font-size: 12px;
		line-height: 1.2; }
	&__btn {
		padding-top: 37px; } }
